@charset 'UTF-8';

// 1. Configuration and helpers
@import
    '1-utils/variables',
    '1-utils/functions',
    '1-utils/colors',
    '1-utils/shared',
    '1-utils/mixins';

// 2. Base stuff
@import
    '2-base/base',
    '2-base/fonts',
    '2-base/typography',
    '2-base/quotes',
    '2-base/lists',
    '2-base/tabs',
    '2-base/accordion',
    '2-base/forms',
    '2-base/buttons';

// 3. Layout-related sections
@import
    '3-layout/global',
    '3-layout/navigation',
    '3-layout/page',
    '3-layout/footer';

// 4. Components
@import
    '4-components/grid',
    '4-components/boxes-news',
    '4-components/insight',
    '4-components/discover',
    '4-components/search',
    '4-components/search-multiple',
    '4-components/call-to-action',
    '4-components/related-services',
    '4-components/contact',
    '4-components/team',
    '4-components/awards',
    '4-components/testimonials',
    '4-components/brands';

// 5. Page-specific styles
@import
    '5-pages/home',
    '5-pages/lawyer',
    '5-pages/insights-page',
    '5-pages/events';


/*
 * Sass Boilerplate
   
 * https://sass-guidelin.es/#architecture
