.wrapper-grid {
  max-width: 100%;
  margin: 0 20px;
 /* display: grid;*/
  grid-gap: 10px;
}


/* no grid support? */

.wrapper-grid {
  display: flex;
  flex-wrap: wrap;
}

.wrapper-grid {
  display: grid;
  margin: 0 auto 6rem;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  grid-auto-rows: minmax(150px, auto);
}

.panel-box {
  /* needed for the flex layout*/
  margin-left: 5px;
  margin-right: 5px;
  flex: 1 1 200px;
  background-color: pink;
  height: 150px;
}

.tall-panel-box {
  grid-row-end: span 2;
  height: 310px;
}

.header-grid,
.footer-grid {
  margin-left: 5px;
  margin-right: 5px;
  flex: 0 1 100%;
  grid-column: 1 / -1;
}

.wrapper-grid > * {
  background-color: #444;
  color: #fff;
  border-radius: 5px;
  padding: 20px;
  font-size: 150%;
  margin-bottom: 10px;
}


/* We need to set the margin used on flex items to 0 as we have gaps in grid.  */

@supports (display: grid) {
  .wrapper-grid > * {
    margin: 0;
  }
}
